import React, { Component } from 'react';
import { Skeleton, Form, Input, Button, Icon, message, Select } from 'antd';
import { connect } from 'react-redux';
import { fail } from 'assert';
import { Link } from 'react-router-dom';

import { getUser, updateUser } from '../../store/actions/auth';
import { getSectors } from '../../store/actions/sector';
import { dynamicSort } from '../utils';
import Hoc from '../../hoc/hoc';
import './user.css';

const Option = Select.Option;

class UserEdit extends Component {

    constructor() {
		super();
		this.state = {
			formLayout: 'vertical',
		};
	}

	state = {
		confirmDirty: false,
	};
	
	componentDidMount() {
		
		if (this.props.token !== undefined && this.props.token !== null) {
		
			this.props.getUser(this.props.token, this.props.currentUser.userId);
			this.props.getSectors(this.props.token);
		}
	}

	UNSAFE_componentWillReceiveProps(newProps) {
		
		if (newProps.token !== this.props.token) {
		
			if (newProps.token !== undefined && newProps.token !== null) {
		
				this.props.getUser(newProps.token, newProps.currentUser.userId);
				this.props.getSectors(newProps.token);
			}
		}
	}

	handleSubmit = e => {
		
		e.preventDefault();
		
		this.props.form.validateFieldsAndScroll((err, values) => {
			if (!err) {
		
				const { currentUser } = this.props;
				const userGetItem = JSON.parse(localStorage.getItem('user'));
				const token = userGetItem.token;
				const sectors = this.props.sectors;
				let is_administrator = false;
				let sector_id = 0;

				if(values.sector === undefined) {
		
					message.warning('O Setor No Pode Ser Nulo.' + 
									'Caso No Tenha Setores Cadastrados, ' +
									'Entre em Contato Com o Administrador do Setor ' + 
									'ou Com o Desenvolvedor');
					this.props.history.push('/informacoes_usuario/');
				} else {
		
					for(let aux = 0; aux < sectors.length; aux ++) {
		
						if(sectors[aux].initials === values.sector) {
							sector_id = sectors[aux].id;
						} 
					}
	
					if(values.userType === 'administrator') {
						is_administrator = true;
					}

					const user = {
						userId: currentUser.id,
						email: currentUser.email,
						username: currentUser.username,
						ramal: values.ramal,
						name: values.name,
						is_administrator: is_administrator,
						sector: sector_id
					};

					if((this.props.updateUser(token, user)) !== fail) {
						message.success('O Usurio ' + currentUser.username + 
										' Teve Suas Informaes Alteradas Com Sucesso!');
					} else {
						message.error('No Foi Possvel Alterar Informaes do Usurio. ' + 
									'Entre em contato com o desenvolvedor!');
					}
					this.props.history.push('/informacoes_usuario/');
				}
			} else {

			}	
		});
	};

    render() {
		const { currentUser } = this.props;
		const sectors = this.props.sectors;
		const { getFieldDecorator } = this.props.form;
		const { formLayout } = this.state;
		const formItemLayout = formLayout === 'vertical'? {
            labelCol: { span: 4 },
            wrapperCol: { span: 14 },
		}
		: null;
		
		let dataSource = {
            innerArray: [
                
            ]
		}

		if(currentUser.sector === null) {
			currentUser.sector = 'No Possui Setor no Momento';			
		}
        
        for(let aux = 0; aux < sectors.length; aux ++) {
            dataSource.innerArray.push(
                {
                    key: sectors[aux].id,
                    initials: sectors[aux].initials,
                    name: sectors[aux].name
                }
			); 
		}

		dataSource.innerArray.sort(dynamicSort('initials'));

		return (
			<Hoc>
				{
					this.props.loading ? (
						<Skeleton active />
					) : (
						<Hoc>
							<h1> Informaes Cadastradas </h1>
							<Form layout = 'vertical' >
								<Form.Item label = 'Nome' { ...formItemLayout } >
									<Input 
										value = { currentUser.name } 
										disabled = { true } 
									/>
								</Form.Item>
								
								<Form.Item label = 'Usurio' { ...formItemLayout } >
									<Input 
										value = { currentUser.username } 
										disabled = { true } 
									/>
								</Form.Item>
								
								<Form.Item label = 'Email' { ...formItemLayout } >
									<Input 
										value = { currentUser.email } 
										disabled = { true } 
									/>
								</Form.Item>

								<Form.Item label = 'Setor' { ...formItemLayout } >
									<Input 
										value = { currentUser.sector } 
										disabled = { true } 
									/>
								</Form.Item>
								
								<Form.Item label = 'Ramal' { ...formItemLayout } >
									<Input 
										value = { currentUser.ramal } 
										disabled = { true } />
								</Form.Item>

								{
									currentUser.is_administrator === true ? (
										<Form.Item label = 'Tipo de Usurio' 
											{ ...formItemLayout } >
											<Input 
												value = 'Administrador' 
												disabled = { true } />
										</Form.Item>
									) : null
								}

								{
									currentUser.is_participant === true ? (
										<Form.Item label = 'Tipo de Usurio' 
											{ ...formItemLayout } >
											<Input 
												value = 'Participante da Reunio'
												disabled = { true } />
										</Form.Item>
									) : null	
								}		
							</Form>
						</Hoc>
					)
				}
				<Hoc>
					{
						sectors.length === 0 ? (
							<Hoc> 
								<h1> Voc No Possui Setor Cadastrado um Antes e Volte Aqui Depois </h1>
								<div align = 'center'>
									<Button 
										type = 'primary'
										style = {{
											marginRight: '40px'
										}} >
										<Link to = { '/criar_setor/' } >
										<Icon 
											style = {{
												marginRight: '10px'
											}}
											type = 'highlight' />
											Criar Setor
										</Link>
									</Button>
									<Button type = 'primary' >
										<Link to = { '/informacoes_usuario/' } >
										<Icon 
											style = {{
												marginRight: '10px'
											}}
											type = 'stop' />
											Cancelar
										</Link>
									</Button>
								</div>
							</Hoc>
						) : (
							<Hoc>
								<h1> Informaes A Serem Alteradas </h1>
								<Form layout = 'vertical' onSubmit = { this.handleSubmit } >
									<Form.Item label = 'Nome Completo' >
										{
											getFieldDecorator('name', {
												rules: [{ 
													required: true, 
													message: 'Por favor, Insira Seu Nome Completo!'
												}],
											})(
												<Input
													prefix = {
														<Icon type = 'user' 
															style = {{ color: 'rgba(0,0,0,.25)' }} 
														/>
													}
													placeholder = 'Nome Completo'
												/>,
											)
										}
									</Form.Item>

									<Form.Item label='Setor' hasFeedback >
										{
											getFieldDecorator('sector', {
											rules: [
												{
													required: false,
													message: 'Por favor, Escolha o Setor do Usurio!',
												}
												],
											})(
												<Select placeholder = 'Escolha o Setor' >
													{ dataSource.innerArray.map(sector => 
														<Option 
															key = { sector.key } 
															value = { sector.initials }>
															{ sector.name }
														</Option>)
													}
												</Select>  
											)
										}
									</Form.Item>

									<Form.Item label = 'Ramal' >
										{
											getFieldDecorator('ramal', {
												rules: [{ 
													required: true, 
													message: 'Por favor, Coloque Seu Ramal!' 
												}],
											})(
												<Input
													prefix = {
														<Icon type = 'phone' 
															style={{ color: 'rgba(0,0,0,.25)' }} 
														/>
													}
													type = 'number'
													placeholder="Ramal"
												/>,
											)
										}
									</Form.Item>

									<Form.Item label = 'Tipo de Usurio' hasFeedback >
										{
											getFieldDecorator('userType', {
											rules: [
												{
													required: true,
													message: 'Por favor, Escolha o Tipo de Usurio!',
												}
												],
											})(
												<Select placeholder = 'Escolha o tipo de usurio' >
													<Option 
														value = 'administrator' > 
															Administrador
													</Option>
													<Option 
														value = 'participant' > 
															Participante da Reunio
													</Option>
												</Select>  
											)
										}
									</Form.Item>

									<Form.Item>
										<div align = 'center'>
											<Button 
												type = 'primary' 
												htmlType = 'submit' 
												style = {{
													marginRight: '20px'
												}}
											>
												<Icon type = 'edit' />
													Alterar Informaes	
											</Button>
											<Button type = 'primary' >
												<Link to = { '/informacoes_usuario/' } >
												<Icon 
													style = {{
														marginRight: '10px'
													}}
													type = 'stop' />
													Cancelar
												</Link>
											</Button>
										</div>
									</Form.Item>
								</Form>
							</Hoc>
						) 
					}
				</Hoc>
			</Hoc>
		);
    }
}

const UserEditForm = Form.create()(UserEdit);

const mapStateToProps = state => {
	return {
		token: state.auth.token,
		currentUser: state.auth.currentUser,
		sectors: state.sector.sectors,
	};
};

const mapDispatchToProps = dispatch => {
	return {
		getUser: (token, userId) => dispatch(getUser(token, userId)),
		updateUser: (token, user) => dispatch(updateUser(token, user)),
		getSectors: token => dispatch(getSectors(token))
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(UserEditForm);